import Vue from "vue";
import store from '@/store';
import Router from 'vue-router'

Vue.use(Router)
const router = new Router({
  routes: [{
    path: '/',
    component: () => import('@/pages/HomePage'),
    name: 'home',
    meta: {
      title: 'home',
      id: 1,
      noCache: true
    }
  }, {
    path: '/product',
    component: () => import('@/pages/ProductPage'),
    name: 'product',
    meta: {
      title: 'product',
      id: 4,
      noCache: true
    }
  }, {
    path: '/job',
    component: () => import('@/pages/JobPage'),
    name: 'job',
    meta: {
      title: 'job',
      id: 5,
      noCache: true
    }
  }, {
    path: '/app',
    component: () => import('@/pages/app/HomePage'),
    name: 'app-home',
    meta: {
      title: 'app-home',
      id: 6,
      noCache: true
    }
  }, {
    path: '/app/product',
    component: () => import('@/pages/app/ProductPage'),
    name: 'app-product',
    meta: {
      title: 'app-product',
      id: 7,
      noCache: true
    }
  }, {
    path: '/app/job',
    component: () => import('@/pages/app/JobPage'),
    name: 'app-job',
    meta: {
      title: 'app-job',
      id: 8,
      noCache: true
    }
  }, {
    path: '/privacy',
    component: () => import('@/pages/PrivacyAgreementPage'),
    name: 'PrivacyAgreement',
    meta: {
      title: 'PrivacyAgreement',
      id: 8,
      noCache: true
    }
  }, {
    path: '/useragreement',
    component: () => import('@/pages/UserAgreementPage'),
    name: 'UserAgreement',
    meta: {
      title: 'UserAgreement',
      id: 8,
      noCache: true
    }
  }]
})

function isMobile() {
  let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
  return flag
}
router.beforeEach(async (to, from, next) => {
  console.log(to.path)
  // console.log(from)
  // let 
  if (to.path === '/' && isMobile()) {
    next({
      path: '/app'
    })
  } else {
    next()
    store.state.pageTabId = to.meta.id
  }
})
export default router